@use "../mixins.scss" as mixin;
@use "../variables.scss" as var;

.feedback-form__header {
  padding: 36px 20px 28px;
  display: flex;
  flex-direction: column;
  row-gap: 24px;
  border-bottom: 1px solid #d6d6d6;
}

.feedback-form__title {
  @include mixin.font-title-mob;
  color: var.$primary-text-color;
  text-align: center;
}

.feedback-form__fieldgroup {
  display: flex;
  flex-direction: column;
  row-gap: 16px;
}

.feedback-form__description {
  @include mixin.font-description-mob;
  color: var.$secondary-text-color;
  text-align: center;
}

.feedback-form__fieldgroup-title {
  @include mixin.font-title-mob;
  color: var.$primary-text-color;
  text-align: center;
  padding-bottom: 4px;
}

.feedback-form__label {
  display: block;
  padding-left: 16px;

  @include mixin.form-label-mob;
  color: var.$primary-text-color;
}

.feedback-form__input {
  @include mixin.font-input-mob;
  display: block;
  background-color: var.$input-primary-backhround;
  padding: 0;
  padding-left: 16px;
  border: none;
  width: 100%;
  min-height: 58px;
  position: relative;

  &::placeholder {
    @include mixin.font-input-mob;
    color: #aaaaaa;
  }

  &:hover {
    background-color: darken(var.$input-primary-backhround, 5%);
  }

  &:active,
  &:focus {
    outline: none;
    box-shadow: inset 0 0 0 4px #e5e5e5;
    background-color: #ffffff;
  }
}

.feedback-form-input-wrapper {
  display: flex;
}

.feedback-form__input-picture {
  width: 58px;
  min-height: 58px;
  padding: 20px;
  background-color: #e6e6e6;
  box-sizing: border-box;
}

.feedback-form__fieldgroup-field-wrapper {
  display: flex;
  flex-direction: column;

  &--list {
    row-gap: 16px;
  }
}

.feedback-form__fieldgroup-control-label {
  cursor: pointer;

  @include mixin.font-input-mob;
  display: flex;
  align-items: center;
}

.feedback-form__fieldgroup-control-input {
  appearance: none;
  margin: 0;
  margin-right: 16px;
  background: #ffffff;
  border-radius: 4px;
  height: 46px;
  box-sizing: border-box;
  cursor: pointer;

  &--radio::before {
    content: "";
    display: block;
    width: 46px;
    height: 46px;
    background-color: #f2f2f2;
    border-radius: 50%;
  }

  &--radio:hover::before {
    background-color: darken(#f2f2f2, 5%);
  }

  &--radio:active::before {
    background-color: darken(#f2f2f2, 8%);
  }

  &--radio:checked::before {
    background: radial-gradient(#000000 8px, #f1f1f1 8px);
  }

  &--radio:checked:hover::before {
    background: radial-gradient(#000000 8px, darken(#f1f1f1, 5%) 8px);
  }

  &--radio:checked:active::before {
    background: radial-gradient(lighten(#000000, 50%) 8px, darken(#f1f1f1, 8%) 8px);
  }

  &--checkbox::before {
    content: "";
    padding: 15px 12px;
    box-sizing: border-box;
    width: 46px;
    height: 46px;
    display: block;
    background-color: #f2f2f2;
    border-radius: 4px;
  }

  &--checkbox:hover::before {
    background-color: darken(#f2f2f2, 5%);
  }

  &--checkbox:active::before {
    background-color: darken(#f2f2f2, 8%);
  }

  &--checkbox:checked::before {
    background: #f1f1f1 url("../img/icons/check.svg") no-repeat center;
  }

  &--checkbox:checked:hover::before {
    background: darken(#f2f2f2, 5%) url("../img/icons/check.svg") no-repeat center;
  }

  &--checkbox:checked:active::before {
    background: darken(#f2f2f2, 8%) url("../img/icons/check.svg") no-repeat center;
    opacity: 0.6;
  }
}

.feedback-form__textarea {
  background-color: #f2f2f2;
  border: none;
  padding: 16px;
}

.feedback-form__textarea::placeholder {
  font-family: "PT Sans", sans-serif;

  @include mixin.font-input-mob;
  color: #aaaaaa;
}

.feedback-form__submit {
  position: relative;
}

.feedback-form__submit::after {
  content: "* — обязательные поля";
  position: absolute;
  bottom: -26px;
  right: 0;
  color: #ababab;
  font-weight: 400;
  font-size: 12px;
  line-height: 21px;
}
